<script>
// import {Quill} from 'vue-quill-editor'
// import {container, ImageExtend, QuillWatch} from './image'
import E from 'wangeditor'

// Quill.register('modules/ImageExtend');
export default {
  name: 'richView',
  props: {
    disabled: Boolean,
    value: String
  },
  data () {
    return {
      str: '',
      editor: null
    }
  },
  mounted () {
    this.editor = new E('#editorElem')
    this.editor.customConfig.uploadImgServer = '/admin/image/upload'
    this.editor.customConfig.uploadFileName = 'picture'
    this.editor.customConfig.uploadImgMaxLength = 1
    this.editor.customConfig.uploadImgHooks = {
      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function (insertImg, result, editor) {
        // 
        // insertImg editor result 
        console.log(result.data)
        //  {url:'....'} 
        var url = 'http://service.fh.81youxi.com' + result.data
        insertImg(url)
        // result  JSON 
      }
    }
    this.editor.customConfig.onchange = (html) => {
      this.str = html
      this.$emit('input', this.str)
    }
    this.editor.create()
  },
  methods: {
    onEditorChange () { // 
      this.$emit('input', this.str)
    }
  },
  watch: {
    value (val) {
      // this.str = val;
      this.editor.txt.html(val)
    }
  }
}
</script>

