  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    height: 100%;
    overflow: scroll;
  }

  .layout-header-bar {
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
  }

  .layout-logo-left {
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
  }

  .menu-icon {
    transition: all .3s;
  }

  .rotate-icon {
    transform: rotate(-90deg);
  }

  .menu-item span {
    display: inline-block;
    overflow: hidden;
    vertical-align: bottom;
    transition: width .2s ease .2s;
  }

  .menu-item i {
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
  }

  .collapsed-menu span {
    width: 0px;
    transition: width .2s ease;
  }

  .collapsed-menu i {
    transform: translateX(5px);
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
  }

  .sider {
    background: white;
  }

  .logo {
    margin-top: 20px;
    height: 100px;
  }
</style>
<template>
  <div class="layout">
    <Layout style="width: 100%;height: 100%">
      <Sider class="sider" ref="side1" hide-trigger collapsible :collapsed-width="78" v-model="isCollapsed">
        <div class="logo">
          <img src="../assets/logo.png" width="30px"/>
          <h2 style="margin-top: 20px" v-show="!isCollapsed"> </h2>
        </div>
        <Menu @on-select="select" accordion :active-name="active" width="auto"
              :class="menuitemClasses">
          <Submenu style="text-align: left" :name="index" v-for="(item,index) in permissionList"
                   v-bind:key="item.id">
            <template slot="title">
              <Icon :type="item.icon"/>
              <span>{{item.name}}</span>
            </template>
            <MenuItem v-show="!isCollapsed" v-bind:key="child.id" v-for="child in item.child"
                      :name="child.index">
                            <span>
                                {{child.name}}</span>
            </MenuItem>
          </Submenu>

        </Menu>
      </Sider>
      <Layout style="text-align: left">
        <Header style="margin-left: 10px;padding: 0" class="layout-header-bar">
          <Row>
            <Col span="1">
              <Icon @click.native="collapsedSider" :class="rotateIcon" :style="{margin: '0 20px'}"
                    type="md-menu" size="24"></Icon>
            </Col>
            <Col span="18">
              <Breadcrumb style="margin-left: 20px;">
                <BreadcrumbItem to="/"></BreadcrumbItem>
                <BreadcrumbItem v-bind:key="index" v-for="(item,index) in $route.meta.crumb" :to="{name:item.index}">
                  {{item.name}}
                </BreadcrumbItem>
              </Breadcrumb>
            </Col>
            <Col span="1">
              <Badge dot style="line-height: 20px">
                <Icon type="ios-notifications-outline" size="26"></Icon>
              </Badge>
            </Col>
            <Col span="4">
              <Dropdown @on-click="select">
                <Icon type="md-person" size="24"/>
                {{$store.state.user.real_name}}
                <DropdownMenu slot="list">
                  <DropdownItem name="login"></DropdownItem>
                  <DropdownItem name="account"></DropdownItem>
                  <DropdownItem name="updatePwd"></DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </Col>
          </Row>
        </Header>

        <Content :style="{margin: '20px',padding:'20px', background: '#fff', minHeight: '260px'}">
          <card>
            <keep-alive>
              <router-view v-if="$route.meta.keepAlive">
              </router-view>
            </keep-alive>
            <router-view v-if="!$route.meta.keepAlive"></router-view>
          </card>
        </Content>
      </Layout>
    </Layout>
  </div>
</template>
<script>
import api from './setting/permission/api'

export default {
  data () {
    return {
      isCollapsed: false,
      permissionList: {},
      active: null
    }
  },
  mounted () {
    this.loadPermission()
  },
  computed: {
    rotateIcon () {
      return [
        'menu-icon',
        this.isCollapsed ? 'rotate-icon' : ''
      ]
    },
    menuitemClasses () {
      return [
        'menu-item',
        this.isCollapsed ? 'collapsed-menu' : ''
      ]
    }
  },
  methods: {
    collapsedSider () {
      this.$refs.side1.toggleCollapse()
    },
    loadPermission () {
      api.node(res => {
        this.permissionList = res.data.data
        this.permissionList.forEach(value => {
          value.child.forEach(v => {
            v.index = v.path.split('/')[2]
          })
        })
        console.log(this.permissionList)
        if (this.$route.name === 'home') {
          // this.select(this.permissionList[0].child[0].path.split('/')[2])
        } else {

        }
        let _this = this
        this.$nextTick(() => {
          _this.active = this.$route.meta.index
        })
      }, () => {
        this.select('login')
      })
    },
    select (key) {
      this.$router.push({
        name: key
      })
    }
  }
}
</script>
